import Vue from 'vue';
import VueRouter from 'vue-router';
Vue.use(VueRouter);
//import 会静态编译  require 动态编译
import Home from '../pages/Home.vue'
// import Follow from '../pages/Follow.vue'
// let Follow = () => { return import(目标组件的地址) }
// let Follow = () => import(目标组件的地址)
let Follow = () => import('../pages/Follow.vue')

// import Column from '../pages/Column.vue'
import User from '../pages/User.vue'
import Login from '../pages/Login.vue'
import Reg from '../pages/Reg.vue'
import Detail from '../pages/Detail.vue'
import ErrorPage from '../components/ErrorPage.vue'
import Buycar from '../pages/Buycar.vue'
import Echarts from '../pages/Echarts.vue'
import BaiDuMap from '../pages/BaiDuMap.vue'

let routes = [
  {path:'/home',component:Home},
  {path:'/follow',component:Follow},
  {path:'/Column',component:r => { require(['../pages/Column'], r) }},
  {path:'/user',component:User},
  {path:'/login',component:Login},
  {path:'/reg',component:Reg},
  {path:'/buycar',component:Buycar},
  {path:'/echarts',component:Echarts},
  {path:'/baidumap',component:BaiDuMap},

  // {path:'/detail/:aid',name:'detail',component:Detail},

  // {path:'/detail/:aid',name:'detail',component:Detail,props:true},
  {path:'/detail/:aid',name:'detail',component:Detail,props:(route)=>({aid:route.params.aid, dataName:route.query.dataName})},
  
  /* {path:'/detail',component:Detail,children:[
    {path:':id',component:Detail}
  ]}, */

  {path:'/',redirect:'/home'},
  {path:'*',component:ErrorPage}
];
let router = new VueRouter({
  routes,
  scrollBehavior (to, from, savedPosition) {
    // return 期望滚动到哪个的位置
    return {x:0,y:0}
  }
});


export default router;


