<template>
  <div id="app">
    <Loading v-show="bLoading"></Loading>
    <Header v-show="bNav"></Header>
   <!--  <transition
      enter-active-class = "animated bounceInRight"
      leave-active-class = "animated bounceOutLeft"
    > -->
      <keep-alive
        :exclude="['Detail']"
      >
        <router-view></router-view>
      </keep-alive>
      
    <!-- </transition> -->
    <Footer v-show="bFoot"></Footer>
  </div>
</template>

<script>
import Header from './components/Header'
import Footer from './components/Footer'
import {mapGetters} from 'vuex'
import * as types from './store/types.js'
export default {
  name: 'aPp',
  components:{
    Header,Footer
  },
  watch:{
    $route:{//路由监听-》数据观测
      handler(to){
        let path = to.path;
        console.log('app', path)

        if(/home|follow|Column/.test(path)){
          this.$store.commit(types.VIEW_NAV,true)
          this.$store.commit(types.VIEW_FOOT,true)
        }
        if(/detail|login|reg/.test(path)){
          this.$store.dispatch(types.VIEW_NAV,false)
          this.$store.dispatch(types.VIEW_FOOT,false)
        }
        if(/user|buycar|echarts|baidumap/.test(path)){
          this.$store.dispatch(types.VIEW_NAV,false)
          this.$store.dispatch(types.VIEW_FOOT,true)
        }

      },
      immediate:true
    }
  },
  computed:mapGetters([
    'bNav','bFoot','bLoading'
  ])
}
</script>

<style>
.loading{
  position: fixed;left:50%;top:50%;z-index: 999;
}
</style>
