<template>
  <div class="buycar">
    <dl v-for="(item,index) in buycar" :key="item._id">
      <dt>标题:{{item.title}}/{{index}}</dt>
      <dd>描述:{{item.des}}</dd>
      <dd>
        <input  type="button" value="+" @click="CHANGE_ITEM({_id:item._id,num:1})">
        {{item.number}}
        <input  type="button" value="-" @click="CHANGE_ITEM({_id:item._id,num:-1})">
      </dd>
      <dd><input  type="button" value="删除" @click="REMOVE_ITEM({_id:item._id})"></dd>
    </dl>
    <input v-show="buycar.length!==0" type="button" value="清空购物车" @click="CLEAR_BUYCAR">
  </div>
</template>
<script>
  import {mapActions,mapGetters} from 'vuex';
  export default {
    computed:mapGetters([
      'buycar'
    ]),
    methods:mapActions([
      'CHANGE_ITEM','REMOVE_ITEM','CLEAR_BUYCAR'
    ])

  }
</script>
<style>
  .buycar{
    margin-top: 50px;
  }
</style>