<template>
  <div class="baidu-map">
    <h3>百度地图</h3>
    <div ref="container" class="bmap"></div> 
    <input type="text" v-model="periphery">
    <input type="button" value="搜索周边" @click="searchPeriphery">
    <hr>
    <input type="text" v-model="start">
    <input type="text" v-model="end">
    <input type="button" value="搜索路线" @click="searchLine">
  </div>
</template>
<script>
export default {
  data(){
    return {
      map:null,
      point:null,

      start:'北京 天安门',
      end:'上海 人民广场',
      periphery:'餐厅'
    }
  },
  methods:{
    searchPeriphery(){
      this.map.clearOverlays();       
      var local =  new BMap.LocalSearch(this.map, {renderOptions: {map: this.map, autoViewport: false}});  
      local.searchNearby(this.periphery,this.point,1000);

      var marker = new BMap.Marker(this.point);        // 创建标注 
      this.map.addOverlay(marker);                     // 将标注添加到地图中 

    },
    searchLine(){
      this.map.clearOverlays(); 
      var driving = new BMap.DrivingRoute(this.map, {renderOptions:{map: this.map, autoViewport: true},policy: BMAP_DRIVING_POLICY_LEAST_DISTANCE});
			driving.search(this.start,this.end);
    }
  },
  mounted(){
    /* navigator.geolocation.getCurrentPosition(
      function(ev){
        console.log('经度',ev.coords.longitude) //经度
        console.log('纬度',ev.coords.latitude) //
      },
      function(error){console.log('error',error)},
    )  */
    this.map = new BMap.Map(this.$refs.container); //创建地图实例  container可以是元素id也可以是元素对象
    this.point = new BMap.Point(121.4978411502, 31.3846399566); //经度纬度
    this.map.centerAndZoom(this.point, 15);  //地图初始化，同时设置地图展示级别
    this.map.enableScrollWheelZoom(true);     //开启鼠标滚轮缩放

    // var myIcon = new BMap.Icon("http://lbsyun.baidu.com/jsdemo/img/fox.gif", new BMap.Size(300,157));

    var marker = new BMap.Marker(this.point);        // 创建标注 
    // var marker = new BMap.Marker(this.point,{icon:myIcon});
    this.map.addOverlay(marker);                     // 将标注添加到地图中 
    // marker.setAnimation(BMAP_ANIMATION_BOUNCE); //跳动的动画
  }
}
</script>
<style scoped>
.bmap{height: 300px;}
</style>


