<template>
  <div class="echarts">
    <h3>ehcarts</h3>

    <!-- <div id="main" style="height:300px"></div> -->
    <div ref="main" style="height:300px" @click="show"></div>

  </div>
</template>
<script>
import echarts from 'echarts';

export default {

  methods:{
    show(){
      console.log('vue 里面的 div 事件')
    },
    show2(params){console.log('mychart事件',params.name)}
  },

  mounted(){

    // var myChart = echarts.init(document.getElementById('main'));
    var myChart = echarts.init(this.$refs.main);

    myChart.showLoading()
    
    axios({
      url:'/data/echarts.json'
    }).then(
      res=> {
        console.log(res.data)
        // 基于准备好的dom，初始化echarts实例
        myChart.hideLoading()
       // 绘制图表
        myChart.setOption(res.data);

      }
    )

    myChart.on('click',this.show2)
    

  }
}
</script>

