// this=={}
import axios from 'axios';
import {
  VIEW_NAV,
  VIEW_FOOT,
  VIEW_LOADING,
  UPDATE_HOME,
  UPDATE_FOLLOW,
  UPDATE_COLUMN,
  UPDATE_DETAIL,
  UPDATE_USER,
  UPDATE_BANNER,
  ADD_ITEM,
  CHANGE_ITEM,
  REMOVE_ITEM,
  CLEAR_BUYCAR
} from './types'

export default {

  [VIEW_NAV] : ({state,commit},payload) => commit(VIEW_NAV,payload),

  [VIEW_FOOT] : ({state,commit},payload) => commit(VIEW_FOOT,payload),

  [VIEW_LOADING] : ({state,commit},payload) => commit(VIEW_LOADING,payload),

  [UPDATE_HOME] : async ({state,commit},payload) => {
    let res = await axios({url:'/api/home',params:{_page:1,_limit:10}});
    commit(UPDATE_HOME,res.data.data)
  },

  /* [CLEAR_HOME] : async ({state,commit},payload) => {
    commit(UPDATE_HOME,[])
  }, */

  [UPDATE_FOLLOW] : async ({state,commit},payload) => {
    let res = await axios({url:'/api/follow',params:{_page:1,_limit:8}});
    commit(UPDATE_FOLLOW,res.data.data)
  },
  
  [UPDATE_COLUMN] : async ({state,commit},payload) => {
    let res = await axios({url:'/api/column',params:{_page:1,_limit:6}});
    commit(UPDATE_COLUMN,res.data.data)
  },

  [UPDATE_DETAIL] : async ({state,commit},{dataName,id}) => {
    let res = await axios({url:`/api/${dataName}/${id}`});
    commit(UPDATE_DETAIL,res.data.data)
  },

  [UPDATE_BANNER] : async ({state,commit},payload) => {
    let res = await axios({url:`/api/banner`,params:{_limit:3}});
    commit(UPDATE_BANNER,res.data.data)
  },

  // 购物车伪代码
  [ADD_ITEM]:({commit,state},payload)=>{ 

    let arr=[...state.buycar];
    let find=false;
    arr.forEach((item,index)=>{
      if(item._id==payload._id){
        item.number++;//数量递增
        find=true;
      }
    });
    if(!find) {
      payload.number=1;//数量
      arr.push(payload);
    }
    commit(ADD_ITEM,arr)
  },
  [CHANGE_ITEM]:({commit,state},payload)=>{
    state.buycar.forEach((item,index)=>{
      if(item._id==payload._id){
        item.number+=payload.num;
      }
    });
    commit(CHANGE_ITEM,[...state.buycar])
  },
  [REMOVE_ITEM]:({commit,state},payload)=>{
    state.buycar.forEach((item,index)=>{
      if(item._id==payload._id){
        state.buycar.splice(index,1);
      }
    });
    commit(REMOVE_ITEM,[...state.buycar]);
    // commit(types.REMOVE_ITEM,state.buycar);
  },
  [CLEAR_BUYCAR]:({commit,state})=>{
    commit(CLEAR_BUYCAR,[]);
  },



  [UPDATE_USER] : ({state,commit},{username,password}) => {
    return axios({
      url:'/api/login',
      method:'post',
      data:{username,password,save:false}
    }).then(
      res=>{
        commit(UPDATE_USER,res.data)//->state
        localStorage.setItem('newsapp_user',JSON.stringify(res.data))//同步一份locastorage
        return res.data
      }
    )
  }

  //this == export default 的 {}
  
}